<template>
    <div class="select-duration">
        <div v-for="(sum, dur) in servicesSum"
             :key="dur"
             class="card"
             :class="{selected: +dur === +duration}"
             @click="changeDuration(+dur)"
        >
            <div class="title__wrapper">
                <p class="title">
                    {{declOfMonth(dur)}}
                </p>
            </div>
            <span v-if="+discounts[dur] !== 0"
                  class="discount"
            >
                -{{discounts[dur]}}%
            </span>
            <p class="price">
                <span v-if="+discounts[dur] !== 0"
                      class="price__old"
                >{{Math.ceil(servicesSum[1] * dur / (100 - discounts[1]) * 100)}}
                    <span class="ruble">
                        &#8381;
                    </span>
                </span>
                {{sum}} <span class="ruble">
                    &#8381;
                </span>
            </p>
        </div>
    </div>
</template>

<script>
    import { defineComponent } from "vue";

    export default defineComponent({
        name: "DurationSelect",
        data: () => {
            return {};
        },
        computed: {
            duration() {
                return this.$store.state.duration;
            },
            servicesSum() {
                return this.$store.state.servicesSum;
            },
            discounts() {
                return this.$store.state.discounts;
            },
        },
        methods: {
            changeDuration(duration) {
                this.$store.dispatch("changeDuration", duration);
                if (duration === 1) this.$store.dispatch("sendUserEvent", "YardSubPeriodMonth");
                else this.$store.dispatch("sendUserEvent", "YardSubPeriodYear");
            },
            declOfMonth(dur) {
                return +dur === 1 ? "Месяц" ?? +dur === 12 : "Год" ?? this.declOfNum(dur);
            },
            declOfNum(num) {
                const cases = [2, 0, 1, 1, 1, 2];
                const titles = ["месяц", "месяца", "месяцев"];

                return num + " " + titles[(num % 100 > 4 && num % 100 < 20) ? 2 : cases[Math.min(num % 10, 5)]];
            },
        },
    });
</script>

<style scoped>
.select-duration {
  display: none;
  position: relative;
  height: auto;
  width: 100%;
  justify-content: space-between;
  align-items: stretch;
  flex-direction: row;
  padding: 16px 16px 0;
  box-sizing: border-box;
  overflow: auto hidden;
  -ms-scroll-snap-type: x mandatory;
  scroll-snap-type: x mandatory;
  scroll-padding-inline-start: 16px;
}

.select-duration::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  display: none;
}

.card {
  width: calc(50% - 4px);
  min-width: 168px;
  display: flex;
  position: relative;
  flex-direction: column;
  padding: 14px 16px;
  border-radius: 14px;
  border: 2px solid #F2F4F7;
  box-sizing: border-box;
}

.card:not(:last-of-type) {
  margin-right: 8px;
}

.card.selected {
  background: #F2F4F7;
}

.card.selected .price {
  color: #1D86FF;
}

.title__wrapper {
  display: flex;
  align-content: center;
  vertical-align: center;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.title {
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: -0.59px;
  color: #6a707f;
}

.card.selected .title {
  color: #061029;
}

.discount {
  display: flex;
  position: absolute;
  right: -2px;
  top: -2px;
  width: fit-content;
  align-self: flex-start;
  padding: 8px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.44px;
  color: #FFFFFF;
  background: #FD6B54;
  border-radius: 14px;
}

.info {
  width: 20px;
  height: 20px;
  display: flex;
  position: absolute;
  right: 13px;
  align-items: center;
  justify-content: center;
  color: #F2F4F7;
  background: #B9C6D1;
  border-radius: 20px;
}

.description {
  margin-bottom: 12px;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: -0.52px;
  color: #061029;
  opacity: 0.6;
}

.price {
  font-weight: 600;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: -0.59px;
  color: #061029;
}

.price__old {
  margin-right: 4px;
  font-weight: 500;
  color: #9b9fa9;
  text-decoration: line-through;
}
</style>